
import Vue from "vue";
import API from "@/api/API";
import store from "@/store";
import rules from "@/services/helpers/validation-rules";
import { mask } from "vue-the-mask";
import FileUploader from "@/components/form-components/FileUploader.vue";

export default Vue.extend({
  name: "Create",
  components: { FileUploader },
  directives: { mask },

  data: () => ({
    rules,
    model: {} as any,
    errorMessages: {} as { [value: string]: Array<string> },
    selects: {
      branches: [] as Array<any>,
      subdivisions: [] as Array<any>
    }
  }),

  async beforeRouteEnter(to, from, next) {
    try {
      const subdivisions = await API.subdivisions().getList();
      const branches = await API.branches().getList();

      next(vm => {
        vm.selects.subdivisions = subdivisions;
        vm.selects.branches = branches;

        vm.$nextTick(() => {
          vm.fullLoaded = true;
        });
      });
    } catch (e) {
      await store.dispatch("alert/showError", e.message);
    }
  },

  methods: {
    async submit(): Promise<void> {
      try {
        if ((this.$refs.form as Vue).validate()) {
          await this.$API.subdivisions().create({ ...this.model });
          await this.$router.push("/subdivisions");
          await this.$store.dispatch(
            "alert/showSuccess",
            this.$t("global-alert.successful_create")
          );
        }
      } catch (e) {
        if (e.hasOwnProperty("errors")) {
          this.errorMessages = e.errors;

          setTimeout(() => {
            this.errorMessages = {};
          }, 2000);
        }
        await this.$store.dispatch("alert/showError", e.message);
      }
    }
  }
});
